import React from "react"
import { Layout, Row, Col } from "antd"
import FA from "react-fontawesome"
import Header from "../../components/PageLayout/Header"
import SidebarWrapper from "../../components/PageLayout/Sidebar"
import Config from "../../../config"
import SEO from "../../components/Seo"

const { facebook, instagram, linkedin } = Config.social
const Contact = () => (
  <Layout className="outerPadding">
    <Layout className="container">
      <SEO
        title="Contact"
        description="Feel free to contact me through any of my social networks."
        path="/contact"
        keywords={[
          "Santiago",
          "Rodriguez",
          "Gonzalez",
          "Front End developer",
          "Javascript",
          "ReactJS",
          "VueJS",
          "Gatsby",
        ]}
      />
      <Header />
      <SidebarWrapper>
        <div className="marginTopTitle">
          <h1 className="titleSeparate">Contact</h1>
        </div>
        <Row gutter={[40, 20]}>
          <Col sm={6}>
            <a
              href={facebook}
              target="_blank"
              label="button"
              rel="noopener noreferrer"
            >
              <FA name="facebook-f" size="3x" />
            </a>
          </Col>
          <Col sm={6}>
            <a
              href={facebook}
              target="_blank"
              label="button"
              rel="noopener noreferrer"
            >
              <FA name="facebook-f" />
            </a>
          </Col>
        </Row>
      </SidebarWrapper>
    </Layout>
  </Layout>
)

export default Contact
